.header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 8;
    background-color: #01012A;
}

.container {
    padding-left: 50px;
    padding-right: 50px;
    padding-top: 10px;

}

.nav {
    height: 3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 1rem;
    /* background-color: #01012A; */
    padding-bottom: 10px;
}

.nav_logo, .nav_toggle {
    color: rgb(255, 255, 255);
    font-weight: bolder;
}

.nav_list {
    display: flex;
    column-gap: 2rem;
}

.nav_link {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: var(--small-font-size);
    font-weight: var(--font-medium);
    transition: 0.3s;
}

.nav_icon,
.nav_close,
.nav_toggle {
    display: none;
}

/* active link */
.active_link,
.nav_link:hover {
    color: Grey;
    font-weight: bolder;
}

/* change background header */
.scroll-header {
    box-shadow: 0 -1px 4px rgba(73, 69, 69, 0.15);
    background-color: #060623;
}


@media screen and (max-width:768px) {

    .header {
        top: initial;
        bottom: 0;
    }

    .nav {
        height: var(--header-height);

    }

    .nav_menu {
        position: fixed;
        bottom: -100%;
        left: 0;
        width: 100%;
        background-color: #01012A;
        padding: 2rem 1.5rem 4rem;
        box-shadow: 0 -1px 4px rgba(166, 150, 203, 0.275);
        border-radius: 1.5rem 1.5rem 0 0;
        transition: 0.3s;
        /* z-index: 6; */
    }

    .show_menu {
        bottom: 0;
    }

    .nav_list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 2rem;
    }

    .nav_icon {
        font-size: 1.2rem;
    }

    .nav_close {
        position: absolute;
        right: 1.3rem;
        bottom: 0.5rem;
        font-size: 1.5rem;
        cursor: pointer;
        color: var(--title-color);
    }

    .nav_close:hover {
        color: grey;
    }

    .nav_toggle {
        font-size: 1.1rem;
        cursor: pointer;
        ;
    }

    .nav_close,
    .nav_icon,
    .nav_toggle {
        display: block;
    }
}

@media screen and (max-width:350px) {
    .nav_menu {
        padding: 2rem 0.25rem 4rem;
    }

    .nav_list {
        column-gap: 0;
    }
}